import { Component } from '@angular/core';
import { LoadingController, NavController } from '@ionic/angular';
import { catchError } from 'rxjs/operators';
import { ICity, IProvince } from 'src/app/models/country.model';
import { IRequestResponse } from 'src/app/models/response.model';
import { IUsers } from 'src/app/models/users.model';
import { AbmService } from 'src/app/services/abm.service';
import { ToolsService } from 'src/app/services/tools.service';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SubjectsService } from 'src/app/services/subjects';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage {
  provinces: IProvince[] = [];
  cities: ICity[] = [];
  registerForm: FormGroup;

  constructor(
    private navCtrl: NavController,
    private abmService: AbmService,
    private subjects: SubjectsService,
    private loadingCtrl: LoadingController,
    private tools: ToolsService,
    private fb: FormBuilder
  ) {
    this.registerForm = this.fb.group({
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      full_name: ['', Validators.required],
      cellphone: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      pass: ['', Validators.required],
      birthdate: ['', Validators.required],
      province: ['', Validators.required],
      city: ['', Validators.required],
      expose_phone: [true],
      expose_email: [true],
      province_id: [-1, Validators.required],
      city_id: [-2, Validators.required],
    });
  }

  ionViewDidEnter() {
    this.loadProvinces();
  }

  loadProvinces() {
    this.subjects.provinces$.subscribe((data: IProvince[]) => {
      this.provinces = [...data];
      setTimeout(() => {
        this.registerForm.get('province_id').setValue(3435907);
        this.loadCities();
      }, 50);
    });
  }

  loadCities() {
    const prov: IProvince = this.provinces.find(
      (x) => x.geonameId === this.registerForm.get('province_id').value
    );
    this.cities = [...prov.cities];
  }

  get isValidName() {
    let firstName = this.tools.capitalizeAllWords(
      this.registerForm.get('first_name').value
    );
    this.registerForm.get('first_name').setValue(firstName);
    return (
      this.registerForm.get('first_name').value.length > 0 ||
      !this.registerForm.get('first_name').touched
    );
  }
  get isValidLastName() {
    let last_name = this.tools.capitalizeAllWords(
      this.registerForm.get('last_name').value
    );
    this.registerForm.get('last_name').setValue(last_name);
    return (
      this.registerForm.get('last_name').value.length > 0 ||
      !this.registerForm.get('last_name').touched
    );
  }
  get isValidPhone() {
    let cellphone = this.tools.validatePhoneNumber(
      this.registerForm.get('cellphone').value
    );
    this.registerForm.get('cellphone').setValue(cellphone);
    return (
      this.registerForm.get('cellphone').value.length >= 10 ||
      !this.registerForm.get('cellphone').touched
    );
  }
  get isValidEmail() {
    return (
      (this.registerForm.value.email.length > 0 &&
        this.tools.validEmail(this.registerForm.value.email)) ||
      !this.registerForm.get('email').touched
    );
  }
  get isValidPass() {
    return (
      this.registerForm.get('pass').value.length > 3 ||
      !this.registerForm.get('pass').touched
    );
  }
  get isValidBirthdate() {
    return (
      this.registerForm.get('birthdate').value.length === 10 ||
      !this.registerForm.get('birthdate').touched
    );
  }

  get isValidProvince() {
    return (
      this.registerForm.get('province_id').value > -2 ||
      !this.registerForm.get('province_id').touched
    );
  }
  get isValidCity() {
    return (
      this.registerForm.get('city_id').value > -2 ||
      !this.registerForm.get('city_id').touched
    );
  }

  get ValidForm() {
    return (
      this.isValidName &&
      this.isValidLastName &&
      this.isValidPhone &&
      this.isValidEmail &&
      this.isValidPass &&
      this.isValidBirthdate &&
      this.isValidProvince &&
      this.isValidCity
    );
  }

  async register() {
    let type = 'create';
    let mjs = 'registrar';
    Object.keys(this.registerForm.controls).forEach((key) => {
      this.registerForm.get(key).markAsTouched();
    });
    if (!this.ValidForm) {
      Swal.fire({
        icon: 'warning',
        title: 'Campos incompletos',
      });
      return;
    }
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 0,
      cssClass: 'custom-loading',
    });

    loading.present();
    this.registerForm
      .get('province')
      .setValue(
        this.tools.getNameListCountries(
          [...this.provinces],
          this.registerForm.get('province_id').value,
          'name'
        )
      );
    this.registerForm
      .get('city')
      .setValue(
        this.tools.getNameListCountries(
          [...this.cities],
          this.registerForm.get('city_id').value,
          'toponymName'
        )
      );
    let form = new FormData();
    form.append('id_user', '-1');
    form.append('first_name', this.registerForm.get('first_name').value);
    form.append('last_name', this.registerForm.get('last_name').value);
    form.append(
      'full_name',
      this.registerForm.get('first_name').value +
        ' ' +
        this.registerForm.get('last_name').value
    );
    form.append('birthdate', this.registerForm.get('birthdate').value);
    form.append('cellphone', this.registerForm.get('cellphone').value);
    form.append('email', this.registerForm.get('email').value);
    form.append('pass', this.registerForm.get('pass').value);
    form.append('province', this.registerForm.get('province').value);
    form.append('city', this.registerForm.get('city').value);
    form.append(
      'province_id',
      this.registerForm.get('province_id').value.toString()
    );
    form.append('city_id', this.registerForm.get('city_id').value.toString());
    form.append(
      'expose_phone',
      this.registerForm.get('expose_phone').value.toString()
    );
    form.append(
      'expose_email',
      this.registerForm.get('expose_email').value.toString()
    );
    form.append('type', type);
    this.abmService
      .ABMUsers(form)
      .pipe(
        catchError((error) => {
          Swal.fire({
            icon: 'error',
            title: 'Error del servidor',
          });
          loading.dismiss();
          throw { ...error };
        })
      )
      .subscribe((response: IRequestResponse<IUsers>) => {
        loading.dismiss();
        if (response.result) {
          Swal.fire({
            icon: 'success',
            title: 'Usuario registrado',
          });
          const sessionEncrypted = btoa(JSON.stringify(response.data));
          localStorage.setItem('token', sessionEncrypted);
          this.navCtrl.navigateForward('/home/schedule');
          return;
        }
        Swal.fire({
          icon: 'error',
          title: 'Error al ' + mjs + ' usuario',
          text: response.message,
        });
      });
  }
}
